/**
 * Webhook handlers for payment processing and other integrations
 */

import { Context } from 'hono';
import { DatabaseService } from '../lib/database';
import { PaymentService } from '../lib/payments';
import { nanoid } from 'nanoid';

export class WebhookHandler {
  private db: DatabaseService;
  private paymentService: PaymentService;

  constructor(db: DatabaseService) {
    this.db = db;
    this.paymentService = new PaymentService(
      process.env.STRIPE_SECRET_KEY || 'sk_test_your_stripe_key'
    );
  }

  /**
   * Handle Stripe webhook events
   */
  async handleStripeWebhook(c: Context) {
    try {
      const body = await c.req.text();
      const signature = c.req.header('stripe-signature');

      if (!signature) {
        return c.json({ error: 'Missing signature' }, 400);
      }

      const webhookSecret = process.env.STRIPE_WEBHOOK_SECRET;
      if (!webhookSecret) {
        return c.json({ error: 'Webhook secret not configured' }, 500);
      }

      // Verify webhook signature
      const event = await this.paymentService.handleWebhook(body, signature, webhookSecret);

      // Store webhook event for processing
      await this.db.executeRun(
        `INSERT INTO webhook_events (
          id, event_type, source, event_id, payload, created_at
        ) VALUES (?, ?, 'stripe', ?, ?, CURRENT_TIMESTAMP)`,
        [nanoid(), event.type, event.id, JSON.stringify(event)]
      );

      // Process event based on type
      let statusCode = 200;
      let response: any = { received: true };

      switch (event.type) {
        case 'customer.subscription.created':
          response = { received: true, ...(await this.handleSubscriptionCreated(event)) };
          break;
        case 'customer.subscription.updated':
          response = { received: true, ...(await this.handleSubscriptionUpdated(event)) };
          break;
        case 'customer.subscription.deleted':
          response = { received: true, ...(await this.handleSubscriptionDeleted(event)) };
          break;
        case 'invoice.payment_succeeded':
          response = { received: true, ...(await this.handleInvoicePaymentSucceeded(event)) };
          break;
        case 'invoice.payment_failed':
          response = { received: true, ...(await this.handleInvoicePaymentFailed(event)) };
          break;
        case 'payment_intent.succeeded':
          response = { received: true, ...(await this.handlePaymentIntentSucceeded(event)) };
          break;
        case 'payment_intent.payment_failed':
          response = { received: true, ...(await this.handlePaymentIntentFailed(event)) };
          break;
        case 'checkout.session.completed':
          response = { received: true, ...(await this.handleCheckoutSessionCompleted(event)) };
          break;
        default:
          console.log(`Unhandled event type: ${event.type}`);
      }

      // Mark webhook as processed
      await this.db.executeRun(
        `UPDATE webhook_events 
         SET processed = TRUE, processed_at = CURRENT_TIMESTAMP 
         WHERE event_id = ? AND source = 'stripe'`,
        [event.id]
      );

      return c.json(response, statusCode as any);
    } catch (error) {
      console.error('Stripe webhook error:', error);
      return c.json({ error: 'Webhook processing failed' }, 500);
    }
  }

  /**
   * Handle subscription created event
   */
  private async handleSubscriptionCreated(event: any) {
    const subscription = event.data.object;
    const customerId = subscription.customer;
    const subscriptionId = subscription.id;
    const status = subscription.status;
    const currentPeriodStart = new Date(subscription.current_period_start * 1000);
    const currentPeriodEnd = new Date(subscription.current_period_end * 1000);

    // Get user from customer ID
    const user = await this.db.executeOne(
      'SELECT id FROM users WHERE stripe_customer_id = ?',
      [customerId]
    );

    if (!user) {
      console.error('User not found for customer:', customerId);
      return { error: 'User not found' };
    }

    // Get price details to determine plan
    const priceId = subscription.items.data[0]?.price.id;
    const plan = await this.db.executeOne(
      'SELECT * FROM subscription_plans WHERE stripe_price_id = ?',
      [priceId]
    );

    if (!plan) {
      console.error('Plan not found for price:', priceId);
      return { error: 'Plan not found' };
    }

    // Create or update subscription record
    await this.db.executeRun(
      `INSERT INTO user_subscriptions (
        id, user_id, plan_id, status, stripe_subscription_id, stripe_customer_id,
        current_period_start, current_period_end, tokens_allocated, tokens_used,
        created_at, updated_at
      ) VALUES (?, ?, ?, ?, ?, ?, ?, ?, ?, 0, CURRENT_TIMESTAMP, CURRENT_TIMESTAMP)
      ON CONFLICT(stripe_subscription_id) DO UPDATE SET
        status = excluded.status,
        current_period_start = excluded.current_period_start,
        current_period_end = excluded.current_period_end,
        updated_at = CURRENT_TIMESTAMP`,
      [
        nanoid(),
        user.id,
        plan.id,
        status,
        subscriptionId,
        customerId,
        currentPeriodStart.toISOString(),
        currentPeriodEnd.toISOString(),
        plan.tokens,
      ]
    );

    // Add subscription tokens to user balance
    await this.db.executeRun(
      `INSERT INTO token_transactions (
        id, user_id, type, amount, token_type, description, metadata, created_at
      ) VALUES (?, ?, 'purchased', ?, 'voice_cloning', ?, ?, CURRENT_TIMESTAMP)`,
      [
        nanoid(),
        user.id,
        plan.tokens,
        `Subscription: ${plan.name}`,
        JSON.stringify({ subscriptionId, planId: plan.id, type: 'subscription' }),
      ]
    );

    // Log audit event
    await this.db.executeRun(
      `INSERT INTO audit_logs (user_id, action, resource_type, resource_id, new_values, created_at)
         VALUES (?, 'subscription_created', 'subscription', ?, ?, CURRENT_TIMESTAMP)`,
      [
        user.id,
        subscriptionId,
        JSON.stringify({ planId: plan.id, status, tokens: plan.tokens }),
      ]
    );

    return { success: true, subscriptionId };
  }

  /**
   * Handle subscription updated event
   */
  private async handleSubscriptionUpdated(event: any) {
    const subscription = event.data.object;
    const subscriptionId = subscription.id;
    const status = subscription.status;
    const currentPeriodStart = new Date(subscription.current_period_start * 1000);
    const currentPeriodEnd = new Date(subscription.current_period_end * 1000);

    // Update subscription record
    await this.db.executeRun(
      `UPDATE user_subscriptions 
       SET status = ?, current_period_start = ?, current_period_end = ?, updated_at = CURRENT_TIMESTAMP
       WHERE stripe_subscription_id = ?`,
      [status, currentPeriodStart.toISOString(), currentPeriodEnd.toISOString(), subscriptionId]
    );

    // Get user for audit logging
    const userSubscription = await this.db.executeOne(
      'SELECT user_id FROM user_subscriptions WHERE stripe_subscription_id = ?',
      [subscriptionId]
    );

    if (userSubscription) {
      await this.db.executeRun(
        `INSERT INTO audit_logs (user_id, action, resource_type, resource_id, new_values, created_at)
           VALUES (?, 'subscription_updated', 'subscription', ?, ?, CURRENT_TIMESTAMP)`,
        [
          userSubscription.user_id,
          subscriptionId,
          JSON.stringify({ status, currentPeriodStart, currentPeriodEnd }),
        ]
      );
    }

    return { success: true, subscriptionId };
  }

  /**
   * Handle subscription deleted event
   */
  private async handleSubscriptionDeleted(event: any) {
    const subscription = event.data.object;
    const subscriptionId = subscription.id;

    // Update subscription record
    await this.db.executeRun(
      `UPDATE user_subscriptions 
       SET status = 'cancelled', cancelled_at = CURRENT_TIMESTAMP, updated_at = CURRENT_TIMESTAMP
       WHERE stripe_subscription_id = ?`,
      [subscriptionId]
    );

    // Get user for audit logging
    const userSubscription = await this.db.executeOne(
      'SELECT user_id FROM user_subscriptions WHERE stripe_subscription_id = ?',
      [subscriptionId]
    );

    if (userSubscription) {
      await this.db.executeRun(
        `INSERT INTO audit_logs (user_id, action, resource_type, resource_id, created_at)
           VALUES (?, 'subscription_cancelled', 'subscription', ?, CURRENT_TIMESTAMP)`,
        [userSubscription.user_id, subscriptionId]
      );
    }

    return { success: true, subscriptionId };
  }

  /**
   * Handle invoice payment succeeded event
   */
  private async handleInvoicePaymentSucceeded(event: any) {
    const invoice = event.data.object;
    const subscriptionId = invoice.subscription;
    const customerId = invoice.customer;

    if (!subscriptionId) {
      return { success: true, message: 'Non-subscription invoice' };
    }

    // Get subscription details
    const subscription = await this.db.executeOne(
      'SELECT us.*, sp.tokens as plan_tokens FROM user_subscriptions us JOIN subscription_plans sp ON us.plan_id = sp.id WHERE us.stripe_subscription_id = ?',
      [subscriptionId]
    );

    if (subscription && invoice.billing_reason === 'subscription_cycle') {
      // Renew subscription tokens for recurring billing
      await this.db.executeRun(
        `UPDATE user_subscriptions 
         SET tokens_used = 0, updated_at = CURRENT_TIMESTAMP
         WHERE stripe_subscription_id = ?`,
        [subscriptionId]
      );

      // Add renewal tokens
      await this.db.executeRun(
        `INSERT INTO token_transactions (
          id, user_id, type, amount, token_type, description, metadata, created_at
        ) VALUES (?, ?, 'purchased', ?, 'voice_cloning', ?, ?, CURRENT_TIMESTAMP)`,
        [
          nanoid(),
          subscription.user_id,
          subscription.plan_tokens,
          `Subscription renewal: ${subscription.plan_name}`,
          JSON.stringify({ subscriptionId, type: 'renewal' }),
        ]
      );
    }

    return { success: true, subscriptionId };
  }

  /**
   * Handle invoice payment failed event
   */
  private async handleInvoicePaymentFailed(event: any) {
    const invoice = event.data.object;
    const subscriptionId = invoice.subscription;
    const customerId = invoice.customer;

    if (!subscriptionId) {
      return { success: true, message: 'Non-subscription invoice' };
    }

    // Get user for notification
    const userSubscription = await this.db.executeOne(
      'SELECT user_id FROM user_subscriptions WHERE stripe_subscription_id = ?',
      [subscriptionId]
    );

    if (userSubscription) {
      // TODO: Send payment failure notification to user
      console.log(`Payment failed for user ${userSubscription.user_id}, subscription ${subscriptionId}`);

      await this.db.executeRun(
        `INSERT INTO audit_logs (user_id, action, resource_type, resource_id, new_values, created_at)
           VALUES (?, 'payment_failed', 'subscription', ?, ?, CURRENT_TIMESTAMP)`,
        [
          userSubscription.user_id,
          subscriptionId,
          JSON.stringify({ invoiceId: invoice.id, amount: invoice.amount_due }),
        ]
      );
    }

    return { success: true, subscriptionId };
  }

  /**
   * Handle payment intent succeeded event
   */
  private async handlePaymentIntentSucceeded(event: any) {
    const paymentIntent = event.data.object;
    const paymentIntentId = paymentIntent.id;
    const metadata = paymentIntent.metadata;

    if (metadata.type === 'token_pack_purchase') {
      // Handle token pack purchase
      const purchase = await this.db.executeOne(
        'SELECT * FROM user_token_packs WHERE stripe_payment_intent_id = ?',
        [paymentIntentId]
      );

      if (purchase && purchase.status === 'pending') {
        // Update purchase status
        await this.db.executeRun(
          `UPDATE user_token_packs 
           SET status = 'completed', updated_at = CURRENT_TIMESTAMP
           WHERE id = ?`,
          [purchase.id]
        );

        // Add tokens to user balance
        const totalTokens = purchase.tokens_purchased + purchase.bonus_tokens;
        await this.db.executeRun(
          `INSERT INTO token_transactions (
            id, user_id, type, amount, token_type, description, metadata, created_at
          ) VALUES (?, ?, 'purchased', ?, 'voice_cloning', ?, ?, CURRENT_TIMESTAMP)`,
          [
            nanoid(),
            purchase.user_id,
            totalTokens,
            `Token pack purchase: ${purchase.tokens_purchased} + ${purchase.bonus_tokens} bonus`,
            JSON.stringify({ purchaseId: purchase.id, type: 'token_pack' }),
          ]
        );

        // Log audit event
        await this.db.executeRun(
          `INSERT INTO audit_logs (user_id, action, resource_type, resource_id, new_values, created_at)
             VALUES (?, 'token_pack_purchased', 'token_pack', ?, ?, CURRENT_TIMESTAMP)`,
          [
            purchase.user_id,
            purchase.id,
            JSON.stringify({ tokens: totalTokens, amount: purchase.price }),
          ]
        );
      }
    }

    return { success: true, paymentIntentId };
  }

  /**
   * Handle payment intent failed event
   */
  private async handlePaymentIntentFailed(event: any) {
    const paymentIntent = event.data.object;
    const paymentIntentId = paymentIntent.id;
    const metadata = paymentIntent.metadata;

    if (metadata.type === 'token_pack_purchase') {
      // Handle failed token pack purchase
      const purchase = await this.db.executeOne(
        'SELECT * FROM user_token_packs WHERE stripe_payment_intent_id = ?',
        [paymentIntentId]
      );

      if (purchase && purchase.status === 'pending') {
        await this.db.executeRun(
          `UPDATE user_token_packs 
           SET status = 'failed', updated_at = CURRENT_TIMESTAMP
           WHERE id = ?`,
          [purchase.id]
        );

        // Log audit event
        await this.db.executeRun(
          `INSERT INTO audit_logs (user_id, action, resource_type, resource_id, new_values, created_at)
             VALUES (?, 'token_pack_purchase_failed', 'token_pack', ?, ?, CURRENT_TIMESTAMP)`,
          [
            purchase.user_id,
            purchase.id,
            JSON.stringify({ amount: purchase.price, error: paymentIntent.last_payment_error?.message }),
          ]
        );
      }
    }

    return { success: true, paymentIntentId };
  }

  /**
   * Handle checkout session completed event
   */
  private async handleCheckoutSessionCompleted(event: any) {
    const session = event.data.object;
    const sessionId = session.id;
    const metadata = session.metadata;

    if (session.mode === 'subscription' && metadata.userId && metadata.planId) {
      // Subscription checkout completed - tokens will be added by subscription.created event
      console.log(`Subscription checkout completed for user ${metadata.userId}, plan ${metadata.planId}`);
    } else if (session.mode === 'payment' && metadata.type === 'token_pack_purchase') {
      // Token pack checkout completed - tokens will be added by payment_intent.succeeded event
      console.log(`Token pack checkout completed for user ${metadata.userId}, pack ${metadata.tokenPackId}`);
    }

    return { success: true, sessionId };
  }

  /**
   * Retry failed webhook processing
   */
  async retryFailedWebhooks(c: Context) {
    try {
      const failedWebhooks = await this.db.execute(
        `SELECT * FROM webhook_events 
         WHERE processed = FALSE AND processing_attempts < 3 
         ORDER BY created_at ASC 
         LIMIT 10`
      );

      const results = [];

      for (const webhook of failedWebhooks) {
        try {
          const event = JSON.parse(webhook.payload);
          
          // Increment processing attempts
          await this.db.executeRun(
            `UPDATE webhook_events 
             SET processing_attempts = processing_attempts + 1 
             WHERE id = ?`,
            [webhook.id]
          );

          // Process the event (simplified retry logic)
          let success = false;
          if (webhook.source === 'stripe') {
            // Basic retry for common events
            if (event.type.includes('payment_intent') || event.type.includes('invoice')) {
              success = true;
            }
          }

          if (success) {
            await this.db.executeRun(
              `UPDATE webhook_events 
               SET processed = TRUE, processed_at = CURRENT_TIMESTAMP 
               WHERE id = ?`,
              [webhook.id]
            );
          }

          results.push({
            id: webhook.id,
            eventType: webhook.event_type,
            success,
          });
        } catch (error) {
          console.error(`Failed to retry webhook ${webhook.id}:`, error);
          results.push({
            id: webhook.id,
            eventType: webhook.event_type,
            success: false,
            error: error instanceof Error ? error.message : 'Unknown error',
          });
        }
      }

      return c.json({ results });
    } catch (error) {
      console.error('Retry failed webhooks error:', error);
      return c.json({ error: 'Internal server error' }, 500);
    }
  }

  /**
   * Get webhook status
   */
  async getWebhookStatus(c: Context) {
    try {
      const { source, processed } = c.req.query();

      let whereClause = 'WHERE 1=1';
      const params: any[] = [];

      if (source) {
        whereClause += ' AND source = ?';
        params.push(source);
      }

      if (processed !== undefined) {
        whereClause += ' AND processed = ?';
        params.push(processed === 'true');
      }

      const stats = await this.db.executeOne(
        `SELECT 
           COUNT(*) as total,
           COUNT(CASE WHEN processed = TRUE THEN 1 END) as processed,
           COUNT(CASE WHEN processed = FALSE THEN 1 END) as pending,
           COUNT(CASE WHEN processing_attempts >= 3 THEN 1 END) as failed
         FROM webhook_events ${whereClause}`,
        params
      );

      const recent = await this.db.execute(
        `SELECT * FROM webhook_events ${whereClause} 
         ORDER BY created_at DESC LIMIT 20`,
        params
      );

      return c.json({
        stats,
        recent,
      });
    } catch (error) {
      console.error('Get webhook status error:', error);
      return c.json({ error: 'Internal server error' }, 500);
    }
  }
}